$cell-size: 100px;
$gap-between-cells: 1px;
$calendar-paddings: 10px;
$calendar-width: ($cell-size * 7) + ($gap-between-cells * 6) +
  ($calendar-paddings * 2);
$cell-border-width: 1px;
$calendar-cell-color: #eee;
$month-length: 31;
$days: (
  "mon": 0,
  "tue": 1,
  "wed": 2,
  "thu": 3,
  "fri": 4,
  "sat": 5,
  "sun": 6,
);
